import Vue from 'vue'
import Router from 'vue-router'  //1.
import LearnRouter from "../components/notused/LearnRouter"; //@
import Fruit from "../components/notused/Fruit";
import Apple from "../components/notused/Apple";
import Banana from "../components/notused/Banana";
import NotFound from "../components/NotFound";
import CustomerTable from "../components/notused/customerTable/CustomerTable";
import Home from "../container/Home";
import Login from "../components/login/Login";
import NProgress from 'nprogress'
import 'nprogress/nprogress.css'
import welcome from "../components/welcome/welcome";
import Users from "../components/user/Users";
import {Notification } from 'element-ui';
import UpPass from "../components/user/UpPass";
import Movies from "../components/movie/Movies";
import addMovie from "../components/movie/addMovie";
import MovieSort from "../components/movie/MovieSort";
import Reports from "../components/report/Reports";
import Actors from "../components/movie/Actors";
import Comments from "../components/commment/Comments";

Vue.use(Router)    //2.use




const router=new Router({    //3.
  linkActiveClass:"active",  //,active
  linkExactActiveClass:"currentActive", //
  routes: [

    {
      path: '/learn/:id',   //()
      name: 'LearnRouter',
      component: LearnRouter
    },
    {
      path:'/fruit',
      name:'Fruit',
      component:Fruit,
      children:[
        {
        path:"Apple",
        component:Apple
      },
        {
          path: "Banana",
          component: Banana
        }
      ]
    },
    {
      path:'/table',
      name:'CustomerTable',
      component:CustomerTable

    },
    /*{
      path:'/',
      redirect:'/fruit'     ///fruit
    },*/
    {
      path:'*',
      component:NotFound     //404
    },
    {
      path:'/home',
      name:'Home',
      component:Home,
      redirect:'welcome',        //homewelcome
      children:[
        {path:'/welcome', component:welcome},
        {
          path:'/users',
          name:Users,
          component:Users
        },
        {
          path:'/upPass',
          name:UpPass,
          component:UpPass
        },
        {
          path:'/movies',
          name:Movies,
          component:Movies,
        },
        {
          path:'/movies/addMovie',
          name:addMovie,
          component:addMovie
        },
        {
          path:'/movieSort',
          name:MovieSort,
          component:MovieSort
        },
        {
          path:'/reports',
          name:Reports,
          component:Reports
        },
        {
          path:'/actors',
          name:Actors,
          component:Actors
        },
        {
          path:'/comments',
          name:Comments,
          component:Comments
        }

      ]
    },

    {
      path:'/login',
      name:'Login',
      component:Login
    },
    {
      path:'/',
      redirect:'/login'
    }

  ]
});
//
router.beforeEach((to, from, next) => {   //tourlfromurlnext
  if (to.path === '/login') {    //
    NProgress.start();
    next();     //
  }
  else if (to.path !== '/login'){   //
    let token = localStorage.getItem('token');    //token
    if (token===null||token===''){    //token
      next('/login');
      Notification.warning('')
    }
    else{           //
      NProgress.start();
      next();
    }
  }
  else{
    NProgress.start();
    next();
  }
});

router.afterEach(() => {
  NProgress.done();
});



export default router;



