<template>
  <div>
    <!---->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/home' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>

    <!-- -->
    <el-card class="box-card" shadow="hover" >
      <!--el-row,:gutterel-col:span-->
      <el-row :gutter="20">
        <el-col :span="7">
          <!---->
          <el-input placeholder=""  class="input-with-select" v-model="queryInfo.query">
            <el-button slot="append" icon="el-icon-search" @click="getCommentList"></el-button>
          </el-input>
        </el-col>
      </el-row>
      <el-table
        :data="commentList"
        style="width: 100%"
        border
      >
        <el-table-column
          label="#"
          type="index">
        </el-table-column>
        <el-table-column
          prop="date"
          label=""
          sortable
          width="180">
        </el-table-column>
        <el-table-column
          prop="comment"
          label=""
          width="500">
          <template slot-scope="scope">
            <span v-html="scope.row.comment"></span>
          </template>
        </el-table-column>
        <el-table-column
          prop="moviename"
          label="">
        </el-table-column>
        <el-table-column
          prop="username"
          label="">
        </el-table-column>
        <el-table-column
          prop="likevalue"
          label=""
          sortable
        width="400">
          <template slot-scope="scope">
            <el-progress :text-inside="true" :stroke-width="20" :percentage="parseInt(scope.row.likevalue)" :format="format"></el-progress>
          </template>
        </el-table-column>
        <el-table-column
          label=""
        width="70">
          <template slot-scope="scope">
            <el-tooltip content="" placement="top" :enterable="false">
              <el-button type="danger" icon="el-icon-delete" size="mini" @click="deleteComment(scope.row.commentid,scope.row.movieid,scope.row.score)"></el-button>
            </el-tooltip>
          </template>
        </el-table-column>
      </el-table>
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="queryInfo.pagenum"
        :page-sizes="[1, 2, 5, 10]"
        :page-size="queryInfo.pagesize"
        layout="total, sizes, prev, pager, next, jumper"
        :total="total">
      </el-pagination>
    </el-card>
  </div>
</template>

<script>
    export default {
        name: "Comments",
        data(){
            return{
                queryInfo:{
                    //
                    query:'',
                    //
                    pagenum:1,
                    //
                    pagesize:5
                },
                total:0,
                commentList:[

                ]
            }
        },
        created() {
            this.getCommentList();
        },
        methods:{
            format:function(percentage){
                return percentage;
            },
        getCommentList() {
            this.$axios.get('/movie/getCommentList?current='+this.queryInfo.pagenum+'&size='+this.queryInfo.pagesize+'&comment='+this.queryInfo.query,
                {headers:{'token':localStorage.getItem('token')}})
                .then(res=>{
                    console.log(res)
                    this.commentList=res.data.data.records
                    this.total=res.data.data.total
                }).catch(err=>{
                    this.$message.error("");
            })
        },
            handleSizeChange(size){
            this.queryInfo.pagesize=size;
            this.getCommentList()
            },
            handleCurrentChange(pageNum){
            this.queryInfo.pagenum=pageNum;
            this.getCommentList()
            },
            deleteComment:function (commentid,movieid,score) {
                this.$confirm(', ?', '', {
                    confirmButtonText: '',
                    cancelButtonText: '',
                    type: 'warning'
                }).then(() => {
                    this.$axios.get('/movie/deleteComment?commentid='+commentid+'&score='+score+'&movieid='+movieid)
                        .then(res=>{
                            console.log(res)
                            if(res.data.data){
                                this.$message.success("")
                                this.getCommentList();
                            }else{
                                this.$message.error("")
                            }
                        }).catch(err=>{
                        this.$message.error("")
                    })
                }).catch(() => {
                    this.$message({
                        type: 'info',
                        message: ''
                    });
                });

            }
        }
    }
</script>

<style lang="less" scoped>
  .el-table{
    margin-top: 15px;
    font-size: 12px;
    span{
      font-size: 12px;
    }
  }
  .el-pagination{
    margin-top: 15px;
  }
</style>



