<template>
  <div>
  <!---->
  <el-breadcrumb separator-class="el-icon-arrow-right">
    <el-breadcrumb-item :to="{ path: '/home' }"></el-breadcrumb-item>
    <el-breadcrumb-item></el-breadcrumb-item>
    <el-breadcrumb-item></el-breadcrumb-item>
  </el-breadcrumb>
  <!-- -->
  <el-card class="box-card" shadow="hover" >
    <div slot="header" class="clearfix">
      <span></span>
      <el-popover
        placement="top"
        width="160"
        v-model="visible">
        <p><i class="el-icon-info redColor"/></p>
        <div style="text-align: right; margin: 0">
          <el-button size="mini" type="text" @click="visible = false"></el-button>
          <el-button type="primary" size="mini" @click="upPass('ruleForm')"></el-button>
        </div>
      <el-button style="float: right; padding: 3px 0" type="text" slot="reference"></el-button>
      </el-popover>
    </div>
    <div>
      <el-form :model="ruleForm" status-icon :rules="rules" ref="ruleForm" label-width="100px" class="demo-ruleForm">
        <el-form-item label="" prop="oldPass">
          <el-input type="password" v-model="ruleForm.oldPass" autocomplete="off"></el-input>
        </el-form-item>
        <el-form-item label="" prop="password">
          <el-input type="password" v-model="ruleForm.password" autocomplete="off"></el-input>
        </el-form-item>
        <el-form-item label="" prop="checkPass">
          <el-input type="password" v-model="ruleForm.checkPass" autocomplete="off"></el-input>
        </el-form-item>
      </el-form>
    </div>
  </el-card>
  </div>
</template>

<script>
    import {mapMutations} from "vuex";
    export default {
        name: "UpPass",
        data(){
            var validateOldPass = (rule, value, callback) => {

                if (value === '') {
                    callback(new Error(''));
                } else {
                    let user=JSON.parse(localStorage.getItem('user'));
                    let password=user.password;
                    if (this.ruleForm.checkPass !== '') {
                        this.$refs.ruleForm.validateField('checkPass');
                    }if(password!==value){
                        callback(new Error(''));
                    }
                    callback();
                }
            };
            var validatePass = (rule, value, callback) => {
                if (value === '') {
                    callback(new Error(''));
                } else {
                    if (this.ruleForm.checkPass !== '') {
                        this.$refs.ruleForm.validateField('checkPass');
                    }
                    if(value.length<6||value.length>14){
                        callback(new Error('6-14'));
                    }
                    if(value===this.ruleForm.oldPass){
                        callback(new Error(''));
                    }
                    callback();
                }
            };
            var validatePass2 = (rule, value, callback) => {
                if (value === '') {
                    callback(new Error(''));
                } else if (value !== this.ruleForm.password) {
                    callback(new Error('!'));
                } else {
                    callback();
                }
            };
            return{
                ruleForm: {
                    oldPass: '',
                    password: '',
                    checkPass: ''
                },
                rules: {
                    oldPass: [
                        { validator: validateOldPass, trigger: 'blur' }
                    ],
                    password: [
                        { validator: validatePass, trigger: 'blur' }
                    ],
                    checkPass: [
                        { validator: validatePass2, trigger: 'blur' }
                    ],
                },
                visible: false,
            }
        },
        methods:{
            ...mapMutations(['delToken']),
            upPass:function (formName) {
                this.$refs[formName].validate((valid) => {   //validatetruefalse
                    if (valid) {
                        let user = JSON.parse(localStorage.getItem('user'));
                        user.password = this.ruleForm.password;
                        let newUser=this.$qs.stringify(user)
                        this.$axios.post('/user/updateUser',newUser)
                            .then(res=>{
                                if(res.data.data.result){
                                    this.$notify.success(",");
                                    this.delToken({token:''});
                                    sessionStorage.removeItem('index')
                                    this.$router.push('/login');

                            }else {
                                this.$message.error('')
                            }
                            }).catch(err=>{
                            this.$message.error('')
                        });
                    }else {
                        console.log('error submit!!');
                        return false;
                    }
                });
                this.visible=false
            }
        }
    }
</script>

<style lang="less" scoped>
.redColor{
  color: red;
  margin-right: 5px;
}
</style>



