<template>
  <div>
  <!---->
  <el-breadcrumb separator-class="el-icon-arrow-right">
    <el-breadcrumb-item :to="{ path: '/home' }"></el-breadcrumb-item>
    <el-breadcrumb-item></el-breadcrumb-item>
    <el-breadcrumb-item></el-breadcrumb-item>
  </el-breadcrumb>

  <!-- -->
  <el-card class="box-card" shadow="hover" >
    <!--el-row,:gutterel-col:span-->
    <el-row :gutter="20">
      <el-col :span="7">
      </el-col>
      <el-col :span="4">
        <el-button type="primary" @click="addSort"></el-button>
      </el-col>
    </el-row>
    <el-table
      border
      :data="sortForm"
      style="width: 100%">
      <el-table-column type="expand">
        <template slot-scope="props">
          <el-form label-position="left" inline class="demo-table-expand">
              <el-tag v-for="item in props.row.movieList" key="index">{{item}}</el-tag>
          </el-form>
        </template>
      </el-table-column>
      <el-table-column
        label=""
        type="index">
      </el-table-column>
      <el-table-column
        label=""
        prop="moviesort">
      </el-table-column>
      <el-table-column
        label=""
        prop="movieNum">
      </el-table-column>
      <el-table-column
        label=""
        width="130">
        <template slot-scope="scope">
          <el-tooltip content="" placement="top" :enterable="false">
            <el-button type="primary" icon="el-icon-edit"  size="mini" @click="openUpSort(scope.row.moviesort)"></el-button>
          </el-tooltip>
          <el-tooltip content="" placement="top" :enterable="false">
            <el-button type="danger" icon="el-icon-delete" size="mini" @click="deleteSort(scope.row.moviesort)"></el-button>
          </el-tooltip>
        </template>
      </el-table-column>
    </el-table>
    <!--layout-->
    <el-pagination
      @size-change="handleSizeChange"
      @current-change="handleCurrentChange"
      :current-page="queryInfo.pagenum"
      :page-sizes="[1, 2, 5, 10]"
      :page-size="queryInfo.pagesize"
      layout="total, sizes, prev, pager, next, jumper"
      :total="total">
    </el-pagination>
  </el-card>
  </div>
</template>

<script>
    export default {
        name: "MovieSort",
        data(){
            return{
                sortForm:[],
                queryInfo:{
                    //
                    pagenum:1,
                    //
                    pagesize:5
                },
                total:0,
            }
        },
        created() {
            this.getSortList();
        },
        methods:{
            getSortList:function () {
                this.$axios.get('/movie/getSortAndMovie?size='+this.queryInfo.pagesize+"&current="+this.queryInfo.pagenum,{headers:{'token':localStorage.getItem('token')}})
                    .then(res=>{
                        console.log(res);
                        this.sortForm=res.data.data.records;
                        this.total=res.data.data.total;
                    }).catch(err=>{
                    this.$notify.error('');
                })
            },
            handleSizeChange:function (pageSize) {
                this.queryInfo.pagesize=pageSize;
                this.getSortList();
            },
            handleCurrentChange:function (pageNum) {
                this.queryInfo.pagenum=pageNum;
                this.getSortList();
            },
            addSort:function () {
                this.$prompt('',  {
                    confirmButtonText: '',
                    cancelButtonText: '',
                    inputPattern: /\S/,
                    inputErrorMessage: ''
                }).then(({ value }) => {
                  this.$axios.get('/movie/addSort?moviesort='+value)
                      .then(res=>{
                          console.log(res.data.data);
                          if(res.data.data){
                              this.$message.success("")
                              this.getSortList()
                          }else{
                              this.$message.error("")
                          }
                      }).catch(err=>{
                          this.$message.error("")
                  })
                }).catch(() => {
                    this.$message({
                        type: 'info',
                        message: ''
                    });
                });
            },
            openUpSort:function (sort) {
                this.$prompt('',  {
                    confirmButtonText: '',
                    cancelButtonText: '',
                    inputPattern: /\S/,
                    inputErrorMessage: '',
                    inputValue:sort
                }).then(({ value }) => {
                    this.$axios.get('/movie/updateSort?moviesort='+value+'&oldMovieSort='+sort)
                        .then(res=>{
                            console.log(res.data.data);
                            if(res.data.data){
                                this.$message.success("")
                                this.getSortList()
                            }else{
                                this.$message.error("")
                            }
                        }).catch(err=>{
                        this.$message.error("")
                    })
                }).catch(() => {
                    this.$message({
                        type: 'info',
                        message: ''
                    });
                });
            },
            deleteSort:function (sort) {
                this.$confirm(', ?', '', {
                    confirmButtonText: '',
                    cancelButtonText: '',
                    type: 'warning'
                }).then(() => {
                    this.$axios.get('/movie/deleteSort?moviesort='+sort)
                        .then(res=>{
                            console.log(res);
                            this.$message.success('')
                            this.getSortList()
                        }).catch(err=>{
                        this.$message.error('')
                    })
                }).catch(() => {
                    this.$message({
                        type: 'info',
                        message: ''
                    });
                });
            }
        }
    }
</script>

<style lang="less" scoped>
  .el-table{
    margin-top: 15px;
    font-size: 12px;
    span{
      font-size: 12px;
    }
  }
  .el-tag{
    margin-right: 6px;
    margin-top: 6px;
    margin-bottom: 6px;
  }
  .el-pagination{
    margin-top: 15px;
  }
</style>



