// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
import axios from 'axios'
import qs from 'qs'
import elementui from 'element-ui'
import 'element-ui/lib/theme-chalk/index.css'
import './assets/css/global.css'       //
import store from "./store/store";
import NProgress from 'nprogress'
import 'nprogress/nprogress.css'
import App from './App'
import router from './router'  //5.

NProgress.configure({showSpinner:false})

Vue.config.productionTip = false;

Vue.prototype.$axios = axios;
axios.defaults.baseURL = '/api';   //
axios.defaults.headers.post['Content-Type'] = 'application/x-www-form-urlencoded;charset=utf-8'
Vue.prototype.$qs = qs;
Vue.use(elementui);

/* eslint-disable no-new */
new Vue({
  el: '#app',
  router,    //5.
  store:store,
  components: { App },
  template: '<App/>'
});


//
axios.interceptors.request.use(config => {
//tokenheadertoken
  NProgress.start();
  if(store.state.token){
    config.headers.common['token']=store.state.token   //vuextoken
  }
 /* const token=localStorage.getItem('token')
  token?config.headers.token=token:null;*/

  return config;
}, error => {
// 
  return Promise.reject(error);
});

//response
axios.interceptors.response.use(
  response => {
    NProgress.done();
    return response;
  },

  error => {  //2XX
    if(error.response){
      switch(error.response.status){
        case 401:
          this.$store.commit('delToken');   //vuextoken
          //localStorage.removeItem('token');
          router.replace({ //
            path: '/login',
            query: { redirect: router.currentRoute.fullPath } // path
          });
      }
    }
    return Promise.reject(error.response);
  }
);




