<template>
    <div>
      <!---->
      <el-breadcrumb separator-class="el-icon-arrow-right">
        <el-breadcrumb-item :to="{ path: '/home' }"></el-breadcrumb-item>
        <el-breadcrumb-item></el-breadcrumb-item>
        <el-breadcrumb-item></el-breadcrumb-item>
      </el-breadcrumb>

     <!-- -->
      <el-card class="box-card" shadow="hover" >
        <!--el-row,:gutterel-col:span-->
        <el-row :gutter="20">
          <el-col :span="7">
            <!---->
            <el-input placeholder=""  class="input-with-select" v-model="queryInfo.query">
              <el-button slot="append" icon="el-icon-search" @click="searchUser"></el-button>
            </el-input>
          </el-col>
          <el-col :span="4">
            <el-button type="primary" @click="addUserDialogVisible=true"></el-button>
          </el-col>
        </el-row>
        <!---->
        <el-dialog
          @close="addUserDialogClosed('addUserForm')"
          title=""
          :visible.sync="addUserDialogVisible"
          width="50%">
          <!---->
          <span>
  <el-form :model="addUserForm" :rules="rules" ref="addUserForm" label-width="100px" class="demo-dynamic"
           status-icon>
     <el-form-item label="" prop="username">
    <el-input type="text" v-model="addUserForm.username" autocomplete="off"></el-input>
  </el-form-item>
      <el-form-item label="" prop="password">
    <el-input type="password" v-model="addUserForm.password" autocomplete="off"></el-input>
  </el-form-item>
  <el-form-item label="" prop="checkpass">
    <el-input type="password" v-model="addUserForm.checkpass" autocomplete="off"></el-input>
  </el-form-item>
     <el-form-item label="" prop="realname">
    <el-input type="text" v-model.number="addUserForm.realname" autocomplete="off"></el-input>
  </el-form-item>
     <el-form-item label="" prop="number">
    <el-input type="text" v-model.number="addUserForm.number" autocomplete="off"></el-input>
  </el-form-item>
  <el-form-item label="" prop="email">
    <el-input v-model="addUserForm.email" autocomplete="off"></el-input>
      </el-form-item>
</el-form>
          </span>
          <!---->
          <span slot="footer" class="dialog-footer">
            <el-button type="primary" @click="addUserSubmit('addUserForm')"> </el-button>
          <el-button @click="addUserDialogVisible = false"> </el-button>
  </span>
        </el-dialog>
        <!---->
        <template >
          <el-table
            :data="userList"
            border
          style="width: 100%">
            <el-table-column type="index" label="#"></el-table-column>
            <el-table-column label="">
              <template slot-scope="scope">   <!--scope.row-->
                  <el-switch
                    @change="adminChanged(scope.row)"
                    v-model="scope.row.rowid"
                    active-value="1"
                    inactive-value="0">
                  </el-switch>
              </template>
            </el-table-column>
            <el-table-column prop="username" label=""></el-table-column>
            <el-table-column prop="realname" label=""></el-table-column>
            <el-table-column prop="number" label=""></el-table-column>
            <el-table-column prop="email" label=""></el-table-column>
            <el-table-column label="" width="100">
              <template slot-scope="scope">
                <el-tooltip content="" placement="top" :enterable="false">
                <el-button type="primary" icon="el-icon-edit" circle size="small" @click="openUpUserDialog(scope.row)"></el-button>
                </el-tooltip>
                <el-tooltip content="" placement="top" :enterable="false">
                <el-button type="danger" icon="el-icon-delete" circle size="small" @click="deleteUser(scope.row.userid)"></el-button>
                </el-tooltip>
                <!---->
                <el-dialog
                  title=""
                  :visible.sync="upUserDialogVisible"
                  width="50%"
                @click="upUserDialogClosed('upUserForm')">
                  <!---->
                  <span>
  <el-form :model="upUserForm" :rules="rules" ref="upUserForm" label-width="100px" class="demo-dynamic"
           status-icon>
     <el-form-item label="" prop="username">
    <el-input type="text" v-model="upUserForm.username" autocomplete="off" :disabled="true"></el-input>
  </el-form-item>
    <el-form-item label="" prop="realname">
    <el-input type="text" v-model.number="upUserForm.realname" autocomplete="off"></el-input>
  </el-form-item>
     <el-form-item label="" prop="number">
    <el-input type="text" v-model.number="upUserForm.number" autocomplete="off"></el-input>
  </el-form-item>
  <el-form-item label="" prop="email">
    <el-input v-model="upUserForm.email" autocomplete="off"></el-input>
      </el-form-item>
</el-form>
          </span>
                  <!---->
                  <span slot="footer" class="dialog-footer">
            <el-button type="primary" @click="upUserSubmit('upUserForm')"> </el-button>
          <el-button @click="upUserDialogVisible = false"> </el-button>
  </span>
                </el-dialog>
              </template>
            </el-table-column>
          </el-table>
          <!--layout-->
          <el-pagination
            @size-change="handleSizeChange"
            @current-change="handleCurrentChange"
            :current-page="queryInfo.pagenum"
            :page-sizes="[1, 2, 5, 10]"
            :page-size="queryInfo.pagesize"
            layout="total, sizes, prev, pager, next, jumper"
            :total="total">
          </el-pagination>
        </template>
      </el-card>
    </div>
</template>

<script>
    export default {

        name: "Users",
        created() {
            this.getUserList();
        },
        data(){
            var validatePass = (rule, value, callback) => {
              if (value !== this.addUserForm.password) {
                    callback(new Error('!'));
                } else {
                    callback();
                }
            };
            var validateRealname = (rule, value, callback) => {

                 if (Number.isFinite(value)) {
                    callback(new Error('!'));
                    }
                 else if(value.length<2||value.length>20){
                     callback(new Error('!'));
                 }
                 else {
                      callback();
                        }
            };
            var validateNumber = (rule, value, callback) => {
                //
            if (!(/^1[3456789]\d{9}$/.test(value))){
                callback(new Error('!'));
            }
                else {
                    callback();
                }
            };
            return{
          userList:[],
                //
                queryInfo:{
                    //
                    query:'',
                    //
                    pagenum:1,
                    //
                    pagesize:5
                },
                total:0,
                /**/
                addUserDialogVisible:false,
                upUserDialogVisible:false,
                addUserForm:{
                    username:'',
                    password:'',
                    checkpass:'',
                    realname:'',
                    number:'',
                    email:''
                },
                upUserForm:{
                    userid:'',
                    rowid:'',
                    username:'',
                    password:'',
                    realname:'',
                    number:'',
                    email:''
                },
                rules:{
                    username: [
                        { required: true, message: '', trigger: 'blur' },
                         { min:3,max:10, message: '3-10', trigger: ['blur', 'change'] },
                         ],
                    password:[
                        { required: true, message: '', trigger: 'blur' },
                        { min:6,max:14, message: '6-14', trigger: ['blur', 'change'] }
                    ],
                    checkpass:[
                        { required: true, message: '', trigger: 'blur' },
                        { validator: validatePass,  trigger: ['blur', 'change'] }
                    ],
                    realname:[
                        { required: true, message: '', trigger: 'blur' },
                        { validator: validateRealname, trigger: ['blur', 'change'] }
                    ],
                    number:[
                        { required: true, message: '', trigger: 'blur' },
                        { validator: validateNumber, trigger: ['blur', 'change'] }
                    ],
                    email:[
                        { required: true, message: '', trigger: 'blur' },
                        { type: 'email', message: '', trigger: ['blur', 'change'] }
                    ]

                }
            }
        },


        methods:{
            getUserList:function () {
                this.$axios.get('/user/getUserList?current='+this.queryInfo.pagenum+'&size='+this.queryInfo.pagesize
                    +'&username='+this.queryInfo.query,
                    {headers:{'token':localStorage.getItem('token')}})
                    .then(res=>{
                   console.log(res)
                        this.userList=res.data.data.records
                        this.total=res.data.data.total
                    }).catch(err=>{
                        this.$message.error("")
                })
            },
            searchUser:function(){
                this.queryInfo.pagenum=1
                this.getUserList()
            },
            //
            handleSizeChange(newSize){
               console.log(newSize);
               this.queryInfo.pagesize=newSize;
                this.getUserList()
            },
            //
            handleCurrentChange(newPage){
                console.log(newPage);
                this.queryInfo.pagenum=newPage;
                this.getUserList()
            },
            //switch
            adminChanged(newUser){
                console.log(newUser);
                let user=this.$qs.stringify(newUser);
                this.$axios.post('/user/updateUser',user)   //userid
                    .then(res=>{
                        if(res.data.data.result){
                            this.$message.success("");
                        }else {
                            this.$message.error('')
                        }
                    }).catch(err=>{
                        this.$message.error('')
                })
            },
            addUserSubmit:function (formName) {
                this.$refs[formName].validate((valid) => {
                    if (valid) {
                        let user=this.$qs.stringify(this.addUserForm);
                        this.$axios.post('/user/addUser',user)
                            .then(res=>{
                                console.log(res)
                                if(res.data.data.result){
                                    this.$message.success('');
                                    this.addUserDialogVisible=false;
                                    this.getUserList();
                                }else{
                                    this.$message.error('');
                                }
                            }).catch(err=>{
                            this.$message.error('')
                        });
                    } else {
                        console.log('error submit!!');
                        return false;
                    }
                });
            },
            openUpUserDialog:function(user){
                this.upUserDialogVisible=true;
                this.upUserForm=user;
                console.log(user)
            },
            upUserSubmit:function(formName){
                this.$refs[formName].validate((valid) => {
                    if (valid) {
                        let user=this.$qs.stringify(this.upUserForm);
                        this.$axios.post('/user/updateUser',user)
                            .then(res=>{
                                console.log(res.data.data.result);
                                if(res.data.data.result){
                                    this.$message.success('');
                                    this.upUserDialogVisible=false;
                                    this.getUserList();
                                }else{
                                    this.$message.error('');
                                }
                            }).catch(err=>{
                            this.$message.error('')
                        });
                    } else {
                        console.log('error submit!!');
                        return false;
                    }
                });
            },
            //
            addUserDialogClosed:function (tablename) {
                    this.$refs[tablename].resetFields();
            },
            //
            upUserDialogClosed:function (tablename) {
                this.$refs[tablename].resetFields();
            },
            deleteUser:function (userid) {
                this.$confirm(', ?', '', {
                    confirmButtonText: '',
                    cancelButtonText: '',
                    type: 'warning'
                }).then(() => {
                  this.$axios.get('/user/deleteUser?userid='+userid)
                      .then(res=>{
                          console.log(res);
                          this.$message.success('')
                          this.getUserList();
                      }).catch(err=>{
                          this.$message.error('')
                  })
                }).catch(() => {
                    this.$message({
                        type: 'info',
                        message: ''
                    });
                });
            }

        },

    }
</script>

<style lang="less"scoped>
.el-table{
  margin-top: 15px;
  font-size: 12px;
}
  .el-pagination{
    margin-top: 15px;
  }
</style>



